*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.navbar {
  height: 3rem;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 10%;
  font-size: 1.5rem;
  background-color: black;
  color: white;
}

.navbar a {
  color: white;
}

.navlinks a {
  text-decoration: none;
  display: inline-block;
  margin-right: 2rem;
}

.searchbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 9% 1rem 10%;
  background-color: rgb(0, 0, 0, 0.8);
  font-size: 1.5rem;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

@media screen and (max-width: 550px) {
  .navbar {
    font-size: 1.2rem;
    padding: 3rem 5%;
  }
  .searchbar {
    padding: 1rem 5%;
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 420px) {
  .navbar {
    font-size: 1rem;
  }
}

.empty {
  display: flex;
  flex-direction: column;
  margin: 5rem 0;
  font-size: 2rem;
  text-align: center;
}

