  }
*/

img {
  width: 300px;
  height: 350px;
  margin-bottom: 1rem;
  cursor: pointer;
}

.product-list {
  margin: 1rem;
  display: flex;
  flex-wrap: wrap;
}

.product-item {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc((100% / 4) - (8rem / 4));
  text-align: center;
  box-shadow: 10px 10px 5px #ccc;
  margin: 1rem;
}

.product-item .hidden {
  visibility: hidden;
  margin: 1rem 0;
}

.product-item .hidden {
  background-color: black;
  color: white;
  border: none;
  border-radius: 2rem;
  padding: 0.8rem;
  font-size: 1.2rem;
}

.product-item:hover .hidden {
  visibility: visible;
}

.product-item .price {
  font-size: 1.4rem;
  font-weight: bold;
}

@media screen and (max-width: 1360px) {
  .product-item {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc((100% / 3) - (6rem / 3));
  }
}

@media screen and (max-width: 1028px) {
  .product-item {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc((100% / 2) - (4rem / 2));
  }

  .product-item img {
    width: 230px;
    height: 280px;
  }
}

@media screen and (max-width: 556px) {
  .product-item {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc((100% / 1) - (2rem / 2));
  }
}

.modal-product {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal-product p {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.modal-product button {
  background-color: black;
  color: white;
  border: none;
  border-radius: 2rem;
  padding: 0.8rem;
  font-size: 1.2rem;
}

button:disabled {
  cursor: not-allowed;
  background-color: gray;
}

.modal-product button:last-child {
  background-color: white;
  color: gray;
  text-decoration: underline;
}

.product-id {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

.hidden p {
  display: none;
}

.product-info {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin-left: 3rem;
}

.product-info p {
  margin-bottom: 1rem;
}

.b {
  font-size: 1.4rem;
  font-weight: bold;
}

@media screen and (max-width: 820px) {
  .product-id img {
    width: 230px;
    height: 280px;
  }
}

@media screen and (max-width: 500px) {
  .product-id {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .product-id .hidden {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .product-info {
    width: unset;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hidden p {
    display: flex;
  }

  .product-info .hidden {
    display: none;
  }
}

.cart {
  display: flex;
}

.check {
  display: flex;
  justify-content: space-between;
  padding: 3rem 5%;
}

.check img {
  width: 100px;
  height: 100px;
}

.check .image {
  width: 20%;
  text-align: center;
}

.check .information {
  width: 50%;
  text-align: left;
}

.check .price {
  width: 20%;
  text-align: right;
}

.pay {
  height: 100vh;
  background-color: rgb(211, 211, 211);
  padding: 3rem 5%;
}

.pay p {
  margin: 1rem;
}

.pay button {
  margin: 1rem;
  background-color: black;
  color: white;
  border: none;
  border-radius: 2rem;
  padding: 0.8rem;
  font-size: 1.2rem;
}

.cart .row1 {
  width: 70%;
}

.cart .row2 {
  width: 30%;
}

.amount {
  display: flex;
}

.amount button {
  border: none;
  background-color: black;
  color: white;
  font-size: 1.5rem;
  border-radius: 0.2rem;
  padding: 0.5rem;
  margin-right: 1rem;
}

.amount p {
  margin-right: 1rem;
  font-size: 1.5rem;
  padding: 0.5rem;
}

.price button {
  border: none;
  background-color: black;
  color: white;
  font-size: 1.5rem;
  border-radius: 0.2rem;
  padding: 0.6rem;
  margin-top: 1rem;
}

@media screen and (max-width: 820px) {
  .cart {
    display: flex;
    flex-direction: column;
  }

  .check {
    display: flex;
    padding: 3rem 10% 3rem 5%;
    font-size: 0.8rem;
  }

  .b {
    font-size: 1rem;
  }

  .pay {
    text-align: center;
  }

  .cart .row1 {
    width: unset;
  }

  .cart .row2 {
    width: unset;
  }
}

@media screen and (max-width: 500px) {
  .check .image {
    display: none;
  }

  .check .information {
    width: 60%;
  }
  .check .price {
    width: 30%;
  }
}

